/* eslint-disable @typescript-eslint/naming-convention */
/* eslint-disable object-shorthand */
import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { LocalService } from '../../services/local.service';
import { RestService } from '../../services/rest.service';
import { UtilityService } from '../../services/utility.service';
import { EventsService } from '../../services/events.service';
import { Location } from '@angular/common';
import { AlertController, IonRouterOutlet, ModalController } from '@ionic/angular';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';
import { ClientinstructionsPage } from '../clientinstructions/clientinstructions.page';

@Component({
  selector: 'app-orderdetails',
  templateUrl: './orderdetails.page.html',
  styleUrls: ['./orderdetails.page.scss'],
})
export class OrderdetailsPage implements OnInit {
  id: any = null;
  loggedUser: any;
  job: any = {};
  off: any;
  currency: any;
  settings: any = {};
  rating: any = 0;
  readonly = false;
  comment: any = '';
  ratingchangedFlag = false;

  isCancellable = false;
  paymentStatus: any = 'Amount Paid';

  mode: any = null;

  // payment disabled flag
  paymentButtonDisabled = false;

  // next service date
  nextServiceDate: any = null;

  // notifications
  notifications = [];

  // extra services
  extraServices = [];
  extraTotal: any = 0;

  // tax settings
  taxSettings = [];
  taxTotal: any = 0;

  canGoBack = false;

  constructor(
    public localService: LocalService,
    public restService: RestService,
    public utilityService: UtilityService,
    public eventsService: EventsService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private location: Location,
    private iab: InAppBrowser,
    private alertCtrl: AlertController,
    private modalCtrl: ModalController,
    private routerOutlet: IonRouterOutlet
  ) {
    this.loggedUser = this.localService.getUser();
    this.settings = this.localService.getSettings();
    this.currency = this.settings.currency_symbol;

    this.activatedRoute.queryParams.subscribe((params) => {
      if (params.id) {
        this.id = params.id;
        // get order data
        this.getOrderDetails();
      } else {
        this.location.back();
      }
      if (params.mode) {
        this.mode = params.mode;
      }
    });
  }

  ngOnInit() {
    this.canGoBack = this.routerOutlet && this.routerOutlet.canGoBack();
  }

  cleanerProfile(id) {
    this.router.navigate(['/cleanerprofile'], { queryParams: { id: id } });
  }

  cleanerBook(id) {
    this.router.navigate(['/book'], { queryParams: { cleaner_id: id } });
  }

  Proceed(type) {
    // type = pending / hold

    // disable payment button
    this.paymentButtonDisabled = true;

    switch (type) {
      case 'pending':
        // payment accept here
        if (this.job.price > 0) {
          // try to charge if existing stripe customer
          this.restService
            .postData(
              'chargeexistingcustomer/' +
                this.loggedUser.id +
                '/' +
                this.job.id +
                '/' +
                this.job.price,
              {}
            )
            .subscribe(
              (success) => {
                // charged successfully using existing stripe_customer_id
                // call api for job status change
                this.utilityService.loadingOpen(
                  'Confirming & notifying cleaners..'
                );
                this.restService
                  .postData('jobstatuschange', {
                    id: this.job.id,
                    status: 'paid',
                  })
                  .subscribe(
                    (done) => {
                      this.utilityService.loadingClose();
                      this.router.navigateByUrl(
                        '/confirmedbooking?job_id=' + this.job.id
                      );
                    },
                    (err) => {
                      this.utilityService.loadingClose();
                      this.router.navigateByUrl('/bookings');
                      this.utilityService.openAlerts(
                        'Oops!',
                        'Error notifying cleaners, please contact us.',
                        'ok'
                      );
                    }
                  );
              },
              (failed) => {
                // 404 = new user to charge
                // 403 = charge failed so again start as new user
                // 400 = error charging
                // open stripe sca in app browser
                const browser = this.iab.create(
                  this.settings.STRIPE_PAYMENT_URL + '/' + this.job.id,
                  '_blank',
                  {
                    location: 'no',
                    hardwareback: 'no',
                    hidenavigationbuttons: 'yes',
                    hideurlbar: 'yes',
                    fullscreen: 'yes',
                    zoom: 'no',
                    clearcache: 'yes',
                    toolbar: 'yes',
                  }
                );

                browser.on('loadstart').subscribe((e) => {
                  //console.log(e.url);
                  if (e.url === this.settings.STRIPE_SUCCESS_PAYMENT_URL) {
                    // payment success
                    browser.close();
                    // call api for job status change
                    this.utilityService.loadingOpen(
                      'Confirming & notifying cleaners..'
                    );
                    this.restService
                      .postData('jobstatuschange', {
                        id: this.job.id,
                        status: 'paid',
                      })
                      .subscribe(
                        (success) => {
                          this.utilityService.loadingClose();
                          this.router.navigateByUrl(
                            '/confirmedbooking?job_id=' + this.job.id
                          );
                        },
                        (err) => {
                          this.utilityService.loadingClose();
                          this.router.navigateByUrl('/bookings');
                          this.utilityService.openAlerts(
                            'Oops!',
                            'Error notifying cleaners, please contact us.',
                            'ok'
                          );
                        }
                      );
                  } else if (
                    e.url === this.settings.STRIPE_FAILED_PAYMENT_URL
                  ) {
                    // payment failed
                    browser.close();
                    this.router.navigateByUrl('/bookings');
                  }
                });
              }
            );
        } else {
          // coupon applied or price is 0
          // call api for job status change
          this.utilityService.loadingOpen('Confirming & notifying cleaners..');
          this.restService
            .postData('jobstatuschange', { id: this.job.id, status: 'paid' })
            .subscribe(
              (success) => {
                this.utilityService.loadingClose();
                this.router.navigateByUrl(
                  '/confirmedbooking?job_id=' + this.job.id
                );
              },
              (err) => {
                this.utilityService.loadingClose();
                this.router.navigateByUrl('/bookings');
                this.utilityService.openAlerts(
                  'Oops!',
                  'Error notifying cleaners, please contact us.',
                  'ok'
                );
              }
            );
        }

        break;

      case 'hold':
        // payment accept here
        // always price should be > 0 if jobsubscription status = hold
        // try to charge if existing stripe customer
        this.restService
          .postData(
            'chargeexistingcustomer/' +
              this.loggedUser.id +
              '/' +
              this.job.id +
              '/' +
              this.job.price,
            {}
          )
          .subscribe(
            (success) => {
              // charged successfully using existing stripe_customer_id
              // call api for job status change
              this.utilityService.loadingOpen(
                'Resuming your upcoming bookings..'
              );
              this.restService
                .postData('user/resumehold', { job_id: this.job.id })
                .subscribe(
                  (done) => {
                    this.utilityService.loadingClose();
                    this.router.navigateByUrl('/subscriptions');
                  },
                  (err) => {
                    this.utilityService.loadingClose();
                    this.router.navigateByUrl('/subscriptions');
                    this.utilityService.openAlerts(
                      'Oops!',
                      'Error resuming upcoming bookings, please contact us.',
                      'ok'
                    );
                  }
                );
            },
            (failed) => {
              // 404 = new user to charge
              // 403 = charge failed so again start as new user
              // 400 = error charging
              // open stripe sca in app browser
              const browser = this.iab.create(
                this.settings.STRIPE_PAYMENT_URL + '/' + this.job.id,
                '_blank',
                {
                  location: 'no',
                  hardwareback: 'no',
                  hidenavigationbuttons: 'yes',
                  hideurlbar: 'yes',
                  fullscreen: 'yes',
                  zoom: 'no',
                  clearcache: 'yes',
                  toolbar: 'yes',
                }
              );

              browser.on('loadstart').subscribe((e) => {
                //console.log(e.url);
                if (e.url === this.settings.STRIPE_SUCCESS_PAYMENT_URL) {
                  // payment success
                  browser.close();
                  // call api for job status change
                  this.utilityService.loadingOpen(
                    'Resuming your upcoming bookings..'
                  );
                  this.restService
                    .postData('user/resumehold', { job_id: this.job.id })
                    .subscribe(
                      (success) => {
                        this.utilityService.loadingClose();
                        this.router.navigateByUrl('/subscriptions');
                      },
                      (err) => {
                        this.utilityService.loadingClose();
                        this.router.navigateByUrl('/subscriptions');
                        this.utilityService.openAlerts(
                          'Oops!',
                          'Error resuming upcoming bookings, please contact us.',
                          'ok'
                        );
                      }
                    );
                } else if (e.url === this.settings.STRIPE_FAILED_PAYMENT_URL) {
                  // payment failed
                  browser.close();
                  this.router.navigateByUrl('/subscriptions');
                }
              });
            }
          );

        break;
      // nothing
      default:
    }
  }

  // rating
  logRatingChange(rating) {
    console.log('changed rating: ', rating);
    // do your stuff
    this.rating = rating;
    this.ratingchangedFlag = true;
  }

  rate() {
    if (!this.ratingchangedFlag) {
      return this.utilityService.openAlerts(
        'Oops!',
        'Please select your rating.',
        'ok'
      );
    }
    if (this.comment === '') {
      return this.utilityService.openAlerts(
        'Oops!',
        'Please write comment',
        'ok'
      );
    }
    this.restService
      .postData('addrating', {
        user_id: this.loggedUser.id,
        cleaner_id: this.job.cleaner_id,
        job_id: this.job.id,
        rating: this.rating,
        comment: this.comment,
      })
      .subscribe(
        (data) => {
          this.utilityService.openAlerts(
            'Thank You',
            'Your review has been submitted.',
            'ok'
          );
          this.router.navigateByUrl('/book');
        },
        (err) => {}
      );
  }

  cancelJob(id) {
    // cancel job
    this.restService
      .postData('canceljob', { job_id: id, status: 'cancelled-user' })
      .subscribe(
        (data) => {
          this.job = data;
        },
        (err) => {}
      );
  }
  async cancelJobPrompt(id) {
    // show alert about penalty
    if (this.settings.free_cancellation_days) {
      const hours = this.settings.free_cancellation_days * 24;
      const alert = await this.alertCtrl.create({
        header: 'Cancel the service?',
        message:
          'Cancellations made with at least ' +
          hours +
          ' hours notice will be fully refunded. However, for cancellations made with less than ' +
          this.settings.paid_cancellation_hours +
          ' hours notice the full charge of the service will be taken.',
        buttons: [
          {
            text: 'No',
            role: 'cancel',
            handler: () => {
              console.log('Cancel clicked');
            },
          },
          {
            text: 'Yes',
            handler: () => {
              console.log('Delete clicked');
              this.cancelJob(id);
            },
          },
        ],
      });
      alert.present();
    } else {
      // no charge required, direct cancel
      this.cancelJob(id);
    }
  }

  getFrequency(name) {
    return this.utilityService.frequencies[name];
  }

  openChat() {
    this.router.navigate(['/chat'], {
      queryParams: {
        user_id: this.job.user.id,
        cleaner_id: this.job.cleaner.id,
      },
    });
  }

  async openInstructions(job) {
    if (!job.extra_services) {
      return this.utilityService.openAlerts(
        'Oops!',
        'No instructions found',
        'ok'
      );
    }
    // open in modal
    const modal = await this.modalCtrl.create({
      component: ClientinstructionsPage,
      cssClass: 'client-instructions-modal',
      backdropDismiss: true,
      showBackdrop: true,
      componentProps: {
        data: job,
      },
    });
    await modal.present();
  }

  async skipClean(id) {
    // calculate time remaining for next service datetime
    const explode_date = this.nextServiceDate.split('-');
    const explode_time = this.job.time.split(':');

    const next_date = new Date(
      Date.UTC(
        explode_date[0],
        explode_date[1] - 1,
        explode_date[2],
        explode_time[0],
        explode_time[1],
        0
      )
    );
    const hours_left = this.diffHours(next_date, new Date());
    let message;
    if (hours_left < 3) {
      // short notice skip/cancellation
      message =
        'Short Notice Cancellation Admin Fee of ' +
        this.settings.currency_symbol +
        '' +
        this.settings.short_notice_skip_charge +
        ' will be charged.';
    } else if (hours_left < 24) {
      // same day skip/cancellation fee
      message =
        'Same Day Cancellation Admin Fee of ' +
        this.settings.currency_symbol +
        '' +
        this.settings.same_day_skip_charge +
        ' will be charged.';
    } else {
      // no skip/cancellation fee
      message = 'Your next cleaning date will be skipped.';
    }

    const alert = await this.alertCtrl.create({
      header: 'Skip next cleaning?',
      message: message,
      buttons: [
        {
          text: 'No',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          },
        },
        {
          text: 'Yes',
          handler: () => {
            console.log('skip clicked');
            this.doSkip(id);
          },
        },
      ],
    });
    await alert.present();
  }

  doSkip(id) {
    this.utilityService.loadingOpen('Processing...');
    this.restService
      .postData('user/' + this.loggedUser.id + '/skip_next_clean', {
        job_id: id,
      })
      .subscribe(
        (data) => {
          this.getOrderDetails();
        },
        (err) => {
          this.utilityService.loadingClose();
          this.utilityService.openAlerts(
            'Oops!',
            'An error occured, Please contact us.',
            'ok'
          );
        }
      );
  }

  diffHours(dt2, dt1) {
    console.log(dt2, dt1);

    let diff = (dt2.getTime() - dt1.getTime()) / 1000;
    diff /= 60 * 60;
    return Math.abs(Math.round(diff));
  }

  getOrderDetails() {
    this.restService.getData('jobdetails/' + this.id).subscribe(
      (data) => {
        this.job = data;

        // check wheather job has extra services or not
        this.extraServices = [];
        this.extraTotal = 0;
        if (this.job.extra_services) {
          this.job.extra_services.forEach((element) => {
            this.extraServices.push(element);
            this.extraTotal += parseFloat(element.price);
          });
          if (this.extraTotal > 0) {
            this.job.total =
              parseFloat(this.job.total) - parseFloat(this.extraTotal);
          }
        }

        // check if job has tax settings
        this.taxSettings = [];
        this.taxTotal = 0;
        if (this.job.tax_applied) {
          this.job.tax_applied.forEach((element) => {
            this.taxSettings.push(element);
            this.taxTotal += parseFloat(element.price);
          });
        }

        // trim whitespace from address
        this.job.address = this.job.address.trim();

        // payable / paid decide
        if (this.job.status === 'pending') {
          this.paymentStatus = 'Amount Payable';
        }
        this.utilityService.loadingClose();

        // coupon condition
        if (data.coupon_id) {
          if (data.coupon.type === 'percentage') {
            this.off =
              ((parseFloat(this.job.total) +
                parseFloat(this.extraTotal) +
                parseFloat(this.taxTotal)) *
                data.coupon.value) /
              100;
            this.off = parseFloat(this.off).toFixed(2);
          } else {
            // flat discount
            this.off = data.coupon.value;
          }
        }
        // show only rating if already rated
        if (data.rating) {
          this.readonly = true;
          this.rating = parseInt(data.rating.rating);
          this.comment = data.rating.comment;
        }

        // check if isCancellable
        if (new Date(data.date) > new Date()) {
          this.isCancellable = true;
        } else {
          console.log('Date already gone');
        }

        // check & calculate next service date
        if (this.job.frequency !== 'oneoff') {
          console.log(this.job);
          // get last paid job
          for (const item of this.job.history) {
            if (item.status === 'paid') {
              this.nextServiceDate = item.date;
            }
          }
        }

        // get notifications
        this.notifications = [];
        this.job.notifications.forEach((element) => {
          if (element.available_alternate) {
            this.notifications.push(element);
          }
        });
      },
      (err) => {}
    );
  }

  actionAlternative(id, type) {
    if (type === 'accept') {
      // accept
      this.restService
        .postData('user/accept_alternative', { id: id })
        .subscribe((data) => {
          this.utilityService.openAlerts(
            'Thank You',
            'Date & Time has been updated.',
            'ok'
          );
          this.getOrderDetails();
        });
    } else {
      // reject
      this.restService
        .postData('user/reject_alternative', { id: id })
        .subscribe((data) => {
          this.getOrderDetails();
        });
    }
  }
}
